.controls {
    width: 100%;
    background-color: #555;
    text-align: center;
    transition: all 0.3s ease;
    color: white;
    font-size: 36px;
    margin: auto;
    height: 8vh;
    overflow: hidden;
    /* position: relative; */
    /* display: grid; */
    /* display:flex; */

    /* justify-content: center; */
    /* align-items: center; */

}

button {
    padding: 14px 28px;
    margin: 15px;
    width: 20%
}

span {
    display: inline-block;
    padding: 5px 28px;
    margin: 1.5vh;
    width: 15%;
    background: #998f8f;
    cursor: pointer;
    border-radius: 30px
        /* height: 4vh; */

}

span:hover {
    /* background: #0c0a0a; */
    background: #2196F3;
    ;

}

.tools {
    margin-top: -2vh;
    padding: 5px 28px;
    position: absolute;
    z-index: 10;
    width: 15%;
    background: #998f8f;
    /* left: 21.65%; */
    left: 31.33%;
    display: none;
    /* transition: display ease-out 5s; */

}
@media(width <= 1440px){
    .tools{
        left: 30.33%;
    }
}
.tools span {
    margin: 2px;
    /* width: auto; */
    width: 13vw;

}

.nav {
    width: 5%
}

#tools:hover {
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
}

.mag:hover~.tools {
    display: block;
    border-bottom-right-radius: 30px;
    border-bottom-left-radius: 30px;
}


.controls:has(.tools:hover) #tools {
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
}


.tools:hover {
    display: block;
    border-bottom-right-radius: 30px;
    border-bottom-left-radius: 30px;
    transition: block ease-in-out 2s;
}